<template>
  <div class="home">
    <div class="home-main">
      <div class="">
        <div class="username">
          {{user.user_name}}
        </div>
        <router-link to="/index">主页</router-link>
        <div class="lgout" @click="logout">
          退出登录
        </div>
      </div>
      <div class="pagation">
        <span class="digt active" @click="steps(0, true)"></span>
        <span class="digt" @click="steps(1,true)"></span>
        <span class="digt" @click="steps(2,true)"></span>
        <span class="digt" @click="steps(3,true)"></span>
        <span class="digt" @click="steps(4,true)"></span>
      </div>
      <div class="list-show">
        <div class="common" ></div>
        <div class="common"></div>
        <div class="common"></div>
        <div class="common"></div>
        <div class="common"></div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue';

import Action from  "../stores/actions"
import Api from "../api/Api";
import qs from "qs";

export default {
  name: 'home',
  data(){
    return {
      step :"",
      index:"",
      user: JSON.parse(this.$store.getters.user) || {},
    }
  },
  components: {
    HelloWorld
  },
  directives:{

  },
  methods:{

    logout(){
      Api.API_LOGOUT(this.user.user_name).then(res=>{
        this.$store.dispatch(Action.ACTION_USER_DELETE);
        this.$store.dispatch(Action.ACTION_TOKEN_DELETE);
        this.$router.push({
          path:"/login",
        })
      })
    },
    steps(index,isClick){
      this.index = index;
      let listShow = document.getElementsByClassName("list-show")[0];
      if(isClick){
        // listShow.style.transition = `all 1s  liner`;
        listShow.style.transitionDuration = `1s`;
        listShow.style.transform = `translateY(-${index * this.step}px)`;
      }else{
        listShow.style.transitionDuration = `0s`;
        listShow.style.transform = `translateY(-${index * this.step}px)`;
      }

    },
    wheel(event){
      let detal = 0;
      if(!event) event = window.event;
      if(event.wheelDelta){
        detal = event.wheelDelta/120;
        if(window.opera) detal = -detal;

      }else if(event.detail){
        detal = event.detail/3;
      }
      if(detal){
        if(detal<0){

        }else{

        }
      }
    },
    MouseScroll(){
      if(window.addEventListener)
        window.addEventListener("DOMMouseScroll",this.wheel,false);
        window.onmousewheel = document.onmousewheel = this.wheel;

    }
  },
  mounted(){
    this.MouseScroll();
    let home_main = document.getElementsByClassName("home-main")[0];
    let common = document.getElementsByClassName("common");
    for (let i = 0; i < common.length; i++) {
      common[i].style.height = document.documentElement.clientHeight + 'px';
    }
    this.step = document.documentElement.clientHeight;
    home_main.style.height = document.documentElement.clientHeight + 'px';
    window.onresize = ()=>{
      this.step = document.documentElement.clientHeight;
      home_main.style.height = document.documentElement.clientHeight + 'px';

      for (let i = 0; i < common.length; i++) {
        common[i].style.height = document.documentElement.clientHeight + 'px';
      }
      this.steps(this.index,false);
    }
  },
  created() {

  }
}
</script>

