import Vue from 'vue'
import Vuex from 'vuex'
import Muts  from "./stores/muts";
import Cookie from "./util/Cookies";
import Actions from "./stores/actions";
Vue.use(Vuex)



export default new Vuex.Store({
  state: {
    Token: Cookie.getCookie("Token") || "", // token
    User: Cookie.getCookie("User")  || "", // User信息
},
  mutations: {
    [Muts.SET_USER]:(state, user)=>{
      state.User = user;
      Cookie.setCookie("User", user);
    },
    [Muts.SET_TOKEN]:(state, token)=>{
      state.Token = token;
      Cookie.setCookie("Token", token);
    },
    [Muts.DELETE_USER]:(state)=>{
      state.User = null;
      Cookie.deleteCookie("User");
    },
    [Muts.DELETE_TOKEN]:(state)=>{
      state.Token = null;
      Cookie.deleteCookie("Token");
    }
  },
  actions: {
    [Actions.ACTION_USER_SET]:({commit},User)=>{
      commit(Muts.SET_USER, User)
    },
    [Actions.ACTION_TOKEN_SET]:({commit},Token)=>{
      commit(Muts.SET_TOKEN, Token)
    },
    [Actions.ACTION_TOKEN_DELETE]:({commit})=>{
      commit(Muts.DELETE_TOKEN)
    },
    [Actions.ACTION_USER_DELETE]:({commit})=>{
      commit(Muts.DELETE_USER)
    },

  },
  getters:{
    token:(state => {
      return state.Token;
    }),
    user:(state => {
      return state.User;
    })
  }
})
