<template>
    <div class="banner" @mouseover="Mover" @mouseleave="Lever">
        <div id="event">

        </div>
        <div class="btn">
            <div class="left">
                <div class="owl owl-pre" @click="pre()" @mouseover="BtnOver()" @mouseleave="BtnLeave()">
                    <span>左边</span>
                </div>
                <div class="owl owl-next" @click="next()" @mouseover="BtnOver_n" @mouseleave="BtnLeave_n">
                    <span>右边</span>
                </div>
            </div>
        </div>
        <div class="banner-bg">
            <el-carousel :interval="4000"  :height="`${Height}px`" ref="carousel" loop>
                <el-carousel-item v-for="(item,index) in banner_img" :key="index">
                    <img :src="item.img_url" alt="">
                    <div class="container">
                        <h3 class="margin-l">
                            <span class="Hi">你好</span>
                            <a href="javascript:void(0)" class="zi-l"> Remenber</a>
                            <a href="javascript:void(0)" class="title-p">{{item.title}}</a> |
                            <a href="javascript:void(0)" class="zi-l">{{item.time}}</a>
                        </h3>
                        <p class="margin-l m-title">
                            {{item.detail}}
                        </p>
                        <Bottoms></Bottoms>
                    </div>
                </el-carousel-item>
            </el-carousel>
        </div>

    </div>
</template>

<script>
    /**
     * 轮播图逻辑分析：
     * 1、知道父元素的宽度
     * 2、知道每个子元素的宽度每次平移的宽度
     * 3、不使用自动轮播  使用 手滑轮播
     * 4、如何判断是根据滑动的距离来判断
     * 5、获取当前点击的位置 在获取 离开的位置  把开始接触的位置 减去 离开的位置  为 复数向左滑 为正向右滑
     * 6、鼠标点击事件  左右点击左右滑动
     */
    // import 'swiper/dist/css/swiper.css'
    // import { Swiper, swiperSlide } from 'vue-swiper-component';
    import Bottoms from  "./Button"
    export default {
        name: "Banner",
        components:{
            // Swiper,swiperSlide
            Bottoms,
        },
        data(){
          return {
              Height:"625",
              isOver:false,
              index:0,
              banner_img:[
                  {
                      id:0,
                      img_url: require("../assets/img/1-22.jpg"),
                      time:"HUJIAN 2019-01-01",
                      title:"这就是爱情？",
                      detail:"所谓爱情也不过是：看上了，追求了，好上了，开心了；不久后，腻了，吵了，淡了，散了",

                  },
                  {
                      id:1,
                      img_url: require("../assets/img/1-14.jpg"),
                      time:"HUJIAN 2019-02-01",
                      title:"什么是人生？",
                      detail:"人生就如呼吸。呼，是为了给自己出口气；吸，是为了给自己争口气。",

                  },
                  {
                      id:2,
                      img_url: require("../assets/img/1-11.jpg"),
                      time:"HUJIAN 2019-03-01",
                      title:"学会生活？",
                      detail:"每过一天，我就在日历上画一个圈，到了星期天我才发现，我把日子过成了省略号",

                  },
                  {
                      id:3,
                      img_url: require("../assets/img/1-10.jpg"),
                      time:"HUJIAN 2019-04-01",
                      title:"走进我的世界？",
                      detail:"我允许你走进我的世界,但不许你在我的世界里走来走去。",
                  },
              ],
              slider:{
                  sliderDown_x:0,
                  sliderDown_y:0,
                  sliderUp_x:0,
                  sliderUp_y:0,
                  isDown:false,
                  sliderMove_x:0,
                  sliderMove_y:0,
              },
              timer:false,
          }
        },
        methods:{
            pre(){
                this.$refs.carousel.prev()
            },
            next(){
                this.$refs.carousel.next()
            },
            BtnOver_n(){
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl-next");
                this.chageBg(els,"background","#fff");
            },
            BtnLeave_n(){
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl-next");
                this.chageBg(els,"background","#000");
            },
            BtnOver(){
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl-pre");
                this.chageBg(els,"background","#fff");
            },
            BtnLeave(){
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl-pre");
                this.chageBg(els,"background","#000");
            },
            Lever(){
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl");
                this.chageBg(els,"opacity",0);
            },
            Mover(){
                // alert("2")
                clearInterval(this.timer);
                this.isOver = !this.isOver;
                let els = document.getElementsByClassName("owl");
                this.chageBg(els,"opacity",1);
            },
            chageBg(el,key,value){
                if(el.length){
                    for (let i = 0; i < el.length; i++) {
                        el[i].style[key] = value;
                    }
                }

            },

            },
        mounted() {
            let width = window.document.documentElement.clientWidth || document.body.clientWidth;
            if(width<780){
                this.Height = 325;
            }
        }
    }
</script>

<style scoped lang="scss">
    .title-p{
        width: 100px;
        height: 30px;
        color: #81db34;
        text-decoration: none;
        overflow: hidden;text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 14px;
        margin: 10px;
    }
    .Hi{
        color: #81db34;
    }
    .m-title{
        transition: all 300ms linear;
        cursor: pointer;
        &:hover{
            color: #81db34;
        }
    }
    .margin-l{
        margin-bottom: 10px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 35px;;
    }
    .container{
        width: 80%;
        position: absolute;
        height: 200px;
        /*background: #00B7FF;*/
        z-index: 900;
        top: 45%;
        left: 10%;
        text-align: left;
        .zi-l{
            text-decoration: none;
            font-family: "Kanit", sans-serif;
            color: rgba(255, 255, 255, 0.7);
            text-transform: uppercase;
            font-size: 14px;
            cursor: pointer;
            transition: all 300ms linear;
            &:hover{
                color: #81db34;
            }
        }
    }
    img{
        width: 100%;
        height: 100%;
    }
    .banner{
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
        .btn{
            width: 100%;
            top: calc(50% - 30px);
            position: absolute;
            z-index: 200;
            .owl{
                display: table;
                cursor: pointer;
                background: #000;
                opacity: 0;
                transition: all 1s linear;
                -webkit-transition: all 1s linear;
                -moz-transition: all 1s linear;
                span{
                    display: table-cell;
                    vertical-align: middle;

                }
            }
            .owl-pre{
                width: 60px;
                height: 60px;

                float: left;
                /*span{*/
                    /*&::before{*/
                        /*content:"\f105";*/
                    /*}*/
                /*}*/
            }
            .owl-next{
                width: 60px;
                height: 60px;
                /*background: #000;*/
                float: right;
            }
            &::before{
                display: table;
                content: "";
            }
            &::after{
                display: table;
                content: "";
                clear:both;
            }
        }
        .banner-bg{
            width: 100%;
            /*transform: translateX();*/
            position: relative;
            z-index: 99;
            transition: all 1s linear;
            &::before{
                display: table;
                content: "";
            }
            &::after{
                display: table;
                content: "";
                clear:both;
            }
            @media screen and (max-width: 780px){
                .slider{
                    float: left;
                    height: 300px;
                    &.slider_01{
                        background: url("../assets/img/1.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.slider_02{
                        background: url("../assets/img/1-1.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.slider_03{
                        background: url("../assets/img/1-2.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                }
            }
            @media screen and (min-width: 780px){
                .slider{
                    /*display: inline-block;*/
                    float: left;
                    height: 625px;
                    &.slider_01{
                        background: url("../assets/img/1.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.slider_02{
                        background: url("../assets/img/1-1.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.slider_03{
                        background: url("../assets/img/1-2.jpg") no-repeat;
                        background-size: 100% 100%;
                    }
                }
            }


        }
    }
</style>