<template>
    <div class="nav"  :class="is_show_nav?'bg_back':''">
        <div class="nav-center">
            <div class="search">
                <div class="form">
                    <div class="show-label">
                        <label class="label"></label>
                    </div>

                    <div class="input">
                        <input type="text"  placeholder="SEARCH">
                    </div>
                </div>
            </div>
            <div class="logo">
                <a href="javasvript:void(0)">
                    <img src="../assets/img/logo-white-green.png" alt="logo">
                </a>
            </div>
            <div class="menu">
                <div class="menu-btn">
                            <span class="menu-home" v-show="!is_show_nav">
                                home
                            </span>
                    <span class="menu-list" @click="show" v-show="!is_show_nav">
                                <img src="../assets/img/menu-align-white.png" alt="menu">
                            </span>
                    <span class="menu-close" @click="close" v-show="is_show_nav" >
                                <img src="../assets/img/menu-close-white.png" alt="menu">
                            </span>
                </div>
            </div>
        </div>
        <div class="nav-show" :class="is_show_nav?'shows':'close'" >
            <div class="lgout" @click="logout">
                退出登录
            </div>
        </div>
    </div>
</template>

<script>
    import Api from "../api/Api";
    import Action from "../stores/actions";

    export default {
        name: "Nav",
        data(){
          return {
              is_show_nav:false,
              user:"",
          }
        },
        created(){
          this.user = this.$store.getters.user;
        },
        mounted(){
          this.scrollCroll();
        },
        methods:{
            scrollCroll(){
                window.onscroll = ()=>{
                    let top = document.documentElement.scrollTop || document.body.scrollTop;
                    let nav = document.getElementsByClassName("nav")[0];
                    if(top>1){
                        nav.style.position="fixed";
                        nav.style.top="0px";
                        nav.style.left="0px";
                        nav.style.background="#000";
                        nav.classList.add("bg_back")
                    }else if(top<=0){
                        nav.classList.remove("bg_back");
                        if(this.is_show_nav==true){

                        }else{
                            nav.style.background="none";
                        }



                        // nav.classList.add("bg");
                    }
                }
            },
            close(){
                this.is_show_nav=false
                // let nav_show = document.getElementsByClassName("nav-show")[0];
                // nav_show.style.transform = "scale(1,0)";
                // nav_show.style.transition ="all 2s linear";
            },
            show(){
                this.is_show_nav=true;
                // let nav_show = document.getElementsByClassName("nav-show")[0];
                // nav_show.style.transform = "scale(1,1)";
                // nav_show.style.transition ="all 2s linear";
            },
            logout(){
                Api.API_LOGOUT(this.user.user_name).then(res=>{
                    this.$store.dispatch(Action.ACTION_USER_DELETE);
                    this.$store.dispatch(Action.ACTION_TOKEN_DELETE);
                    this.$router.push({
                        path:"/login",
                    })
                })
            },
        }

    }
</script>

