<template>
    <div class="power">
        <div class="center">
            <div class="common" v-for="item in data_list">
                <img :src="item.img_url" alt="">
                <div class="blog_show">
                    <div class="author-detail">
                        <span class="left-time">
                            {{item.time}}
                        </span>
                            <span class="right-author">
                            <i>By</i>
                            {{item.author}}
                        </span>
                    </div>
                    <p class="title">
                        Nature, in the broadest sense, is the natural, physical, or material world or universe.
                    </p>
                    <p class="detail">
                        In the broadest sense, is the natural, physical, or material world or universe...
                    </p>
                    <div class="footer">
                        <div class="tom">
                            <i>in</i>
                            <span>
                                IMAGE, ENTERTAINMENT
                             </span>
                        </div>
                        <div class="commit">
                            <span class="img"></span>5

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"powerwriter",
    data(){
      return {
         data_list:[
              {
                  id:0,
                  img_url: require("../assets/img/1-22.jpg"),
                  time:"2019-01-01",
                  author:"hujian",
                  title:"这就是爱情？",
                  detail:"所谓爱情也不过是：看上了，追求了，好上了，开心了；不久后，腻了，吵了，淡了，散了",
              },
              {
                  id:1,
                  img_url: require("../assets/img/1-14.jpg"),
                  time:"2019-01-01",
                  author:"hujian",
                  title:"什么是人生？",
                  detail:"人生就如呼吸。呼，是为了给自己出口气；吸，是为了给自己争口气。",

              },
              {
                  id:2,
                  img_url: require("../assets/img/1-11.jpg"),
                  time:"2019-01-01",
                  author:"hujian",
                  title:"学会生活？",
                  detail:"每过一天，我就在日历上画一个圈，到了星期天我才发现，我把日子过成了省略号",

              },
              {
                  id:3,
                  img_url: require("../assets/img/1-10.jpg"),
                  time:"2019-01-01",
                  author:"hujian",
                  title:"走进我的世界？",
                  detail:"我允许你走进我的世界,但不许你在我的世界里走来走去。",
              },
                 {
                     id:0,
                     img_url: require("../assets/img/1-22.jpg"),
                     time:"2019-01-01",
                     author:"hujian",
                     title:"这就是爱情？",
                     detail:"所谓爱情也不过是：看上了，追求了，好上了，开心了；不久后，腻了，吵了，淡了，散了",
                 },
                 {
                     id:1,
                     img_url: require("../assets/img/1-14.jpg"),
                     time:"2019-01-01",
                     author:"hujian",
                     title:"什么是人生？",
                     detail:"人生就如呼吸。呼，是为了给自己出口气；吸，是为了给自己争口气。",

                 },
                 {
                     id:2,
                     img_url: require("../assets/img/1-11.jpg"),
                     time:"2019-01-01",
                     author:"hujian",
                     title:"学会生活？",
                     detail:"每过一天，我就在日历上画一个圈，到了星期天我才发现，我把日子过成了省略号",

                 },
                 {
                     id:3,
                     img_url: require("../assets/img/1-10.jpg"),
                     time:"2019-01-01",
                     author:"hujian",
                     title:"走进我的世界？",
                     detail:"我允许你走进我的世界,但不许你在我的世界里走来走去。",
                 },
          ],
          width:"",
          heiList : new Array(),
      }
    },
    methods: {
        createElement(){
            let divpre = document.getElementsByClassName("common");
            for(let i =0; i<divpre.length;i++){

                divpre[i].style.width = `${this.width}px`;
            }
        },
        fineMin(arrH){ // 找到最矮 的一排 
            let num = 0;
            for(let i=0; i<arrH.length; i++){
                num = Math.min(arrH[num], arrH[i]) == arrH[num]? num:i;
            }
            return num;
        },
        chang(){

            let winDom = document.getElementsByClassName("center")[0].offsetWidth;
            let n = Math.floor(winDom/this.width);
            if(n<=0){
                return;
            }
            let center = (winDom - n*this.width) /2;
            let aDiv = document.getElementsByClassName("common");
            for(let i=0; i<aDiv.length;i++){
                let j = i%n;
                if(this.heiList.length==n){
                    let min = this.fineMin(this.heiList);
                    aDiv[i].style.left = `${center + min*(this.width+10)}px`;
                    aDiv[i].style.top = `${10 + this.heiList[min]}px`;
                    this.heiList[min] += aDiv[i].offsetHeight+10; // 最矮的一排添加高度  在开始算起
                }else{
                    this.heiList[j]  = aDiv[i].offsetHeight;  // 获取实际宽度;
                    aDiv[i].style.left = `${center + i*(this.width+10)}px`;
                    aDiv[i].style.top = '0px';
                }
            }
            let centers = document.getElementsByClassName("center")[0];
            let max = Math.max.apply(null,this.heiList);
            // alert(max);
            centers.style.height = `${max}px`;
            this.heiList = [];
        }
    },
     mounted() {
            let width = document.documentElement.clientWidth  || document.body.clientWidth;
            if(width<480){
                this.width = 320;
                this.createElement();
                setTimeout(()=>{
                    this.chang();
                },10)

            }else{

                this.width = 380;
                this.createElement();
                setTimeout(()=>{
                    this.chang();
                },10)


            }
         window.onresize = ()=>{
             this.chang();
         }

    },
}
</script>



<style lang="scss" scoped>
    img{
        width: 100%;
    }
.power{
    width: 100%;
    padding-top: 50px;
    background: #f5f4f4;
    padding-bottom: 50px;
    /**/
    .center{
        width: 95%;
        /*height: 800px;*/
        /*overflow: hidden;*/
        margin: 0 auto;
        position: relative;
        .common{
            background: #fff;
            position: absolute;
            /*white-space:nowrap;*/
            .blog_show{
                width: 100%;
                padding: 20px;
                font-size: 12px;
                box-sizing: border-box;
                .author-detail{
                    width: 100%;
                    &:before{
                        display: table;
                        content: '';
                    }
                    &:after{
                        display: table;
                        content: "";
                        clear: both;
                    }
                    .left-time{

                        color: #808080;
                        display: inline-block;
                        float: left;
                    }
                    .right-author{
                        color: #808080;
                        display: block;
                        float: right;
                        text-align: right;
                    }
                }
                .title{
                    font-weight: 500;
                    font-size: 23px;
                    line-height: 1.25;
                    text-align: left;
                    font-family: "Josefin Sans", sans-serif;
                    color: #333;
                    cursor: pointer;
                    padding: 10px 0px;
                    transition:  all 300ms linear;
                    &:hover{
                        color: #81db34;
                    }
                }
                .detail{
                    font: 500 italic 15px/21px "Roboto", sans-serif;
                    color: #808080;
                    font-size: 14px;
                    text-align: left;
                    padding-bottom: 22px;
                }
                .footer{
                    width: 100%;
                    padding-bottom: 10px;
                    &:before{
                        display: table;
                        content: '';
                    }
                    &:after{
                        display: table;
                        content: "";
                        clear: both;
                    }
                    .tom{
                        float: left;
                        background: -webkit-linear-gradient(330deg, #81db34 0%, #34d9da 100%);
                        color: #34d9da;
                        display: inline-block;
                        -webkit-text-fill-color: transparent;
                        font-family: "Kanit", sans-serif;
                        /*text-transform: uppercase;*/
                        letter-spacing: 1px;
                        background-clip: text;
                        i{
                            margin-right: 10px;
                        }

                    }
                    .commit{
                        float: right;

                        .img{
                            width: 20px;
                            height: 20px;

                            display: inline-block;
                            background: url("../assets/img/comment-icon-gray.png") no-repeat;
                            vertical-align: middle;
                            padding-right: 10px;
                        }
                    }
                }
            }

        }
    }
}

</style>
