import Ajax from "../util/Request";
import Qs from  "qs";

let Server = "http://192.168.43.216:8080"
// Api
export default {
    API_LOGIN : (params) => Ajax.post(`${Server}/users/login`, Qs.stringify(params)),
    API_REGISTER: (params) => Ajax.post(`${Server}/users/register`, Qs.stringify(params)),
    API_LOGOUT:  (username) => Ajax.get(`${Server}/users/logout?username=${username}`),
    API_LOGOUT_GITHUB:  (params) => Ajax.post(`/github/login/oauth/access_token`, params),
}