import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Cookie from "./util/Cookies"
Vue.config.productionTip = false
Vue.use(ElementUI);
// 路由拦截器
router.beforeEach((to,from,next)=>{
  let userName = store.getters.user;
  let Token = store.getters.token;
  if(to.path !=="/login" && !Token ){ // 判断
    if(to.path =="/register" && !Token) { // 判断页面是否具有权限
      next()
    }else{
      next("/login");
    }

  }else if(to.meta.permission){ // 判断页面是否具有权限
      userName == "admin" ? next(): next("/403");
  }else if(to.path =="/login" && Token){ // 判断页面是否具有权限
      next("/")
  }else if(to.path =="/register" && Token){ // 判断页面是否具有权限
    next("/")
  }else{ // 前往跳转
    next()
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
